@import '../_fonts'

.section.main_car_animation
  background-color: #97e5ec
  min-height: 100vh
  min-width: 100%

  .container
    text-align: left
    height: 100vh
    //border: 1px dotted yellow

    .block-program
      top: 60px
      left: 0
      opacity: 0.7
      color: black
      font-family: $f_helvetica
      font-weight: 300
      font-size: 35.1px
      line-height: 50.71px
      text-transform: uppercase
      //border: 1px dotted red
      width: 300px
      position: absolute

    .block-top
      position: absolute
      top: 100px
      left: 50%
      width: 400px
      font-family: $f_helvetica
      font-weight: 300
      font-size: 16px
      line-height: 26px

      .top-1
        .button
          display: inline-block
          margin-right: 10px
          padding: 15px 30px
          border: 2px solid #3aa8b3
          border-radius: 100px
          cursor: pointer


        .button:last-child
          margin-right: 0

        .button:hover
          color: white
          background-color: #3aa8b3

    .content
      position: relative
      width: 630px
      height: 300px
      top: 60%
      left: 50%
      transform: translateX(-50%) translateY(-50%)

      .cleaner
        position: absolute
        bottom: 35px
        right: 75px
        width: 140px
        height: 300px
        background-image: url("../pics/section.main_car_animation.cleaner.png")
        background-repeat: no-repeat
        //border: 1px dotted red

      .car
        position: absolute
        bottom: 0
        width: 630px
        height: 210px

        background-image: url("../pics/section.main_car_animation.car.png")
        background-repeat: no-repeat
        //border: 1px dotted red

    .block-bottom
      position: absolute
      bottom: 100px
      left: 50%
      width: 550px

      .text
        font-family: $f_helvetica
        font-weight: 300
        font-size: 14px
        line-height: 21px
        opacity: 0.4

